.projects-main {
  padding-bottom: 30px;
  overflow: hidden;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
  &__send {
    width: 22px;
    flex-shrink: 0;
    cursor: pointer;
    img {
      width: 100%;
    }
  }
  &__tabs-head {
    margin-top: 70px;
  }
  .swiper {
    overflow: unset;
  }
  .tab {
    width: auto !important;
    height: 44px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #E5E5E5;
    padding: 0 20px;
    font-family: 'PlayfairDisplay';
    font-size: 24px;
    line-height: 120%;
    font-variant: lining-nums;
    color: #83878D;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      border-color: #2B2A29;
      color: #2B2A29;
    }
  }
  .active-tab {
    border-color: #2B2A29;
    color: #2B2A29;
  }
  &__tabs-body {
    margin-top: 35px;
  }
  .tab__content {
    display: none;
    &--active {
      display: block;
    }
  }
  .projects__cards {
    margin-top: 0;
  }
  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 32px;
    margin-top: 60px;
  }
  &__btn {
    min-width: 276px;
    span {
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 992px) {
  .projects-main {
    &__title {
      font-size: 50px;
    }
    &__tabs-head {
      margin-top: 60px;
    }
    .tab {
      height: 42px;
      padding: 0 18px;
      font-size: 20px;
    }
  }
}

@media screen and (max-width: 700px) {
  .projects-main {
    &__btn {
      min-width: unset;
      padding-left: 32px;
      padding-right: 32px;
    }
  }
}

@media screen and (max-width: 576px) {
  .projects-main {
    padding-bottom: 20px;
    &__title {
      font-size: 30px;
    }
    &__tabs-head {
      margin-top: 30px;
    }
    .tab {
      height: 38px;
      padding: 0 14px;
      font-size: 16px;
    }
    &__tabs-body {
      margin-top: 30px;
    }
    &__nav {
      flex-direction: column;
      margin-top: 30px;
    }
    &__btn {
      width: 100%;
    }
  }
}
