.product-main {
  padding-bottom: 32px;
  overflow: hidden;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    margin-top: 32px;
  }
  &__name {
    font-size: 14px;
    line-height: 120%;
    background: #F5F5F5;
    padding: 4px 14px;
  }
  &__group {
    width: 50%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
  &__art {
    font-size: 16px;
    line-height: 120%;
    color: #83878D;
  }
  &__info {
    font-size: 16px;
    line-height: 120%;
  }
  &__star {
    display: flex;
    align-items: flex-end;
    gap: 8px;
    i {
      width: 24px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
      line-height: 105%;
    }
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 30px;
    margin-top: 48px;
  }
  &__slider {
    width: 50%;
  }
  &__desc {
    width: 50%;
  }
  .mySwiper2 {
    .swiper-slide {
      border: 1px solid #E5E5E5;
      padding: 45px;
      img {
        max-width: 390px;
        margin: 0 auto;
      }
    }
  }
  .mySwiper {
    margin-top: 16px;
    .swiper-slide {
      position: relative;
      border: 1px solid #E5E5E5;
      padding: 10px 10px 29% 10px;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
    .swiper-slide-thumb-active {
      border-color: #83878D;
    }
    .play {
      width: 12px;
      pointer-events: none;
      &:after {
        width: 24px;
        height: 24px;
      }
      img {
        position: relative !important;
        height: auto;
        object-fit: cover;
      }
    }
  }
  .swiper-slide {
    display: flex;
    justify-content: center;
    align-content: center;
    img {
      width: 100%;
      object-fit: contain;
    }
  }
  .play {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 28px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 56px;
      height: 56px;
      background: rgba(255, 255, 255, 0.8);
      border-radius: 50%;
    }
    &:hover {
      &:after {
        background: #fff;
      }
    }
    img {
      position: relative;
      z-index: 2;
      width: 100%;
    }
  }
  &__desc-top {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 8px 16px;
    &2 {
    justify-content: flex-end;
  }
  }
  .video-consultation {
    display: flex;
    align-items: center;
    gap: 12px;
    i {
      width: 24px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
      line-height: 120%;
    }
  }
  &__group2 {
    display: flex;
    align-items: center;
    gap: 16px;
  }
  .send {
    width: 22px;
    cursor: pointer;
    img {
      width: 100%;
    }
  }
  .favorite {
    width: 18px;
    display: flex;
    align-items: center;
    cursor: pointer;
    img {
      width: 100%;
    }
  }
  &__price {
    display: flex;
    align-items: flex-end;
    gap: 16px;
    margin-top: 32px;
    span:nth-child(1) {
      font-size: 36px;
      line-height: 105%;
      font-weight: 700;
    }
    span:nth-child(2) {
      font-size: 18px;
      line-height: 105%;
      color: #83878D;
      text-decoration: line-through;
      margin-bottom: 5px;
    }
  }
  &__checkboxes {
    margin-top: 32px;
  }
  &__subtitle {
    font-family: 'PlayfairDisplay';
    font-size: 24px;
    line-height: 120%;
  }
  &__checkboxes-wrap {
    display: flex;
    flex-wrap: wrap;
    gap: 16px 32px;
    margin-top: 16px;
  }
  &__checkbox {
    position: relative;
    display: flex;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:before {
        opacity: 1;
      }
    }
    label {
      position: relative;
      font-size: 16px;
      line-height: 120%;
      font-weight: 400;
      padding-left: 28px;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 16px;
        height: 16px;
        border: 1px solid #2B2A29;
        border-radius: 50%;
      }
      &:before {
        content: '';
        position: absolute;
        top: 4px;
        left: 4px;
        width: 8px;
        height: 8px;
        background: #2B2A29;
        border-radius: 50%;
        opacity: 0;
        transition: all 0.3s;
      }
    }
  }
  &__btns {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 32px / 2));
    gap: 32px;
    margin-top: 32px;
  }
  &__btn {
    padding-left: 16px;
    padding-right: 16px;
  }
  &__group3 {
    margin-top: 32px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 16px;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 24px;
    &-icon {
      width: 32px;
      height: 32px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #F6F6F6;
      border-radius: 50%;
      cursor: pointer;
      img {
        max-width: 18px;
        margin: auto;
      }
      img:nth-child(2) {
        display: none;
      }
    }
    &-icon--active {
      background: #2B2A29;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
    &-text {
      display: flex;
      align-items: center;
      gap: 16px;
      font-size: 16px;
      line-height: 120%;
      font-weight: 300;
    }
    &-icon2 {
      width: 24px;
      cursor: pointer;
      img {
        width: 100%;
      }
    }
  }
  &__delivery {
    margin-top: 32px;
    &-group {
      margin-top: 16px;
    }
    &-title {
      font-size: 18px;
      line-height: 120%;
    }
    &-text {
      margin-top: 12px;
      p {
        font-size: 16px;
        line-height: 140%;
        font-weight: 300;
      }
      a {
        font-weight: 400;
        text-decoration: underline;
        text-decoration-color: #E22429;
        text-underline-offset: 3px;
        &:hover {
          text-decoration: unset;
        }
      }
    }
  }
  &__group4 {
    margin-top: 32px;
  }
  &__text {
    margin-top: 16px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:3;
    -webkit-box-orient: vertical;
    &--active {
      display: block;
      overflow: unset;
    }
    p {
      font-size: 16px;
      line-height: 140%;
      font-weight: 300;
    }
  }
  &__btn-show {
    display: inline-flex;
    align-items: center;
    gap: 8px;
    text-underline-offset: 3px;
    cursor: pointer;
    transition: all 0.3s;
    margin-top: 12px;
    &--active {
      span:nth-child(1) {
        display: none;
      }
      span:nth-child(2) {
        display: block !important;
      }
      i {
        transform: rotate(-180deg);
      }
    }
    &:hover {
      text-decoration: underline;
    }
    span {
      font-size: 16px;
      line-height: 120%;
    }
    span:nth-child(2) {
      display: none;
    }
    i {
      width: 12px;
      display: flex;
      align-items: center;
      transition: all 0.3s;
      img {
        width: 100%;
      }
    }
  }
  &__characteristic {
    margin-top: 32px;
    &-list {
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
      margin-top: 32px;
      &--active {
        li {
          display: flex !important;
        }
      }
      li {
        position: relative;
        display: flex;
        gap: 30px;
        &:after {
          content: '';
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          height: 1px;
          border-bottom: 1px dashed #E5E5E5;
        }
        span {
          font-size: 16px;
          line-height: 120%;
          background: #fff;
        }
        span:nth-child(1) {
          min-width: 170px;
          font-weight: 300;
          span {
            position: relative;
            z-index: 2;
            min-width: unset;
          }
        }
        span:nth-child(2) {
          position: relative;
          z-index: 2;
          flex-grow: 1;
        }
      }
      li {
        &:nth-child(3) ~ li {
          display: none;
        }
      }
    }
    .product-main__btn-show {
      margin-top: 16px;
    }
  }
  &__info2 {
    display: flex;
    align-items: center;
    gap: 14px;
    background: #797979;
    padding: 16px;
    margin-top: 32px;
    &-img {
      position: relative;
      width: 65px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
      span {
        position: absolute;
        z-index: 2;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        img {
          width: 24px;
        }
      }
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      color: #fff;
    }
  }
  &__calc {
    display: flex;
    align-items: center;
    gap: 22px;
    margin-top: 32px;
  }
  &__calc2 {
    flex-direction: column;
    align-items: start;
  }
  &__select {
    min-width: 215px;
    max-width: 100%;
  }
  .cartcalc {
    display: flex;
    align-items: center;
    gap: 2px;
    .ccalc-minus,
    .ccalc-plus {
      width: 24px;
      height: 24px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #F6F6F6;
      cursor: pointer;
      svg {
        width: 17px;
        height: auto;
      }
    }
    input {
      width: 44px;
      height: 24px;
      text-align: center;
      background: transparent;
      border: none;
      font-size: 18px;
      line-height: 120%;
    }
  }
  .calc-info {
    position: relative;
    margin-left: 18px;
    &:hover {
      .calc-info__hidden {
        opacity: 1;
        pointer-events: auto;
      }
    }
    &__show {
      width: 24px;
      height: 24px;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid #2B2A29;
      border-radius: 50%;
      cursor: pointer;
      svg {
        width: 6px;
        height: auto;
      }
    }
    &__hidden {
      position: absolute;
      left: -40px;
      bottom: 100%;
      width: 250px;
      padding-bottom: 8px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      &:after {
        content: '';
        position: absolute;
        left: 47px;
        bottom: 4px;
        transform: rotate(-45deg);
        width: 11px;
        height: 11px;
        background: #fff;
      }
    }
    &__list {
      display: grid;
      grid-template-columns: 100%;
      gap: 4px;
      background: #fff;
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
      padding: 15px;
      li {
        position: relative;
        display: flex;
        justify-content: space-between;
        gap: 8px;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          width: 100%;
          height: 1px;
          border: 1px dashed #E5E5E5;
        }
        p {
          position: relative;
          z-index: 1;
          font-size: 14px;
          line-height: 120%;
          font-weight: 300;
          background: #fff;
        }
        p:nth-child(1) {
          padding-right: 4px;
        }
        p:nth-child(2) {
          padding-left: 4px;
          span {
            font-weight: 400;
          }
        }
      }
    }
  }
  &__modal-btn {
    background: transparent;
    padding: 0;
    margin-top: 16px;
    &:hover {
      span {
        text-decoration: unset;
      }
    }
    span {
      font-size: 16px;
      line-height: 120%;
      text-decoration: underline;
      text-decoration-color: #E22429;
      text-underline-offset: 3px;
    }
  }
  &__branding {
    margin-top: 32px;
    &-list {
      display: grid;
      grid-template-columns: 100%;
      padding-left: 10px;
      margin-top: 16px;
      li {
        position: relative;
        font-size: 16px;
        line-height: 140%;
        font-weight: 300;
        padding-left: 16px;
        &:after {
          content: '';
          position: absolute;
          top: 9px;
          left: 0;
          width: 2px;
          height: 2px;
          background: #2B2A29;
          border-radius: 50%;
        }
      }
    }
  }
}
.products2 {
  padding: 32px 0;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title {
    width: 100%;
  }
  &__cards {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 90px / 4));
    gap: 30px;
    margin-top: 40px;
  }
  &__btn {
    gap: 10px;
    padding-left: 34px;
    padding-right: 34px;
    margin-top: 40px;
    &:hover {
      svg path {
        stroke: #fff;
      }
    }
    i {
      width: 15px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      font-size: 18px;
    }
  }
}
.expert {
  padding: 32px 0 64px;
  &__inner {
    text-align: center;
    border: 1.5px solid #000000;
    padding: 45px;
  }
  &__wrap {
    max-width: 910px;
    margin: 0 auto;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 40px;
    p {
      font-size: 18px;
      line-height: 120%;
    }
  }
  &__slider {
    padding: 0 58px;
    margin-top: 32px;
  }
  .swiper-slide {
    img {
      width: 100%;
    }
  }
  &__user {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-top: 32px;
    &-img {
      width: 60px;
      img {
        width: 100%;
      }
    }
    &-desc {
      margin-top: 10px;
    }
    &-name {
      font-size: 18px;
      line-height: 120%;
    }
    &-post {
      font-size: 12px;
      line-height: 120%;
      color: #83878D;
      margin-top: 2px;
    }
  }
}
.galley-modal {
  padding: 0;
  &__title {
    border-bottom: 1px solid #E5E5E5;
    padding: 28px 60px 28px 32px;
  }
  .modal__dialog {
    max-width: 1440px;
    width: calc(100% - 30px);
    padding: 0;
  }
  .modal__close {
    top: 40px;
    right: 40px;
  }
  .modal__content {
    padding: 32px 96px;
  }
  .product-main__slider {
    max-width: 820px;
    width: 100%;
    height: 642px;
    display: flex;
    align-content: stretch;
    gap: 16px;
    margin: 0 auto;
  }
  .mySwiper2 {
    width: calc(100% - 180px);
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
  }
  .mySwiper {
    width: 164px;
    order: -1;
    margin-top: 0;
    @media screen and (min-width: 577px) {
      .swiper-wrapper {
        overflow-y: auto;
        &::-webkit-scrollbar {
          width: 4px;
        }
        &::-webkit-scrollbar-track {
          background: #fff;
          border-radius: 4px;
        }
        &::-webkit-scrollbar-thumb {
          background: #83878D;
          border-radius: 11px;
        }
      }
      .swiper-slide {
        width: calc(100% - 12px);
        margin-right: 12px;
      }
    }
  }
  .swiper-button-prev {
    left: 24px;
    margin-top: 0;
  }
  .swiper-button-next {
    right: 24px;
    margin-top: 0;
  }
}

@media screen and (max-width: 1100px) {
  .product-main {
    .calc-info {
      position: relative;
      margin-left: 18px;
      &__hidden {
        left: unset;
        right: -40px;
        &:after {
          left: unset;
          right: 47px;
        }
      }
      &__list {
        display: grid;
        grid-template-columns: 100%;
        gap: 4px;
        background: #fff;
        box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
        padding: 15px;
        li {
          position: relative;
          display: flex;
          justify-content: space-between;
          gap: 8px;
          &:after {
            content: '';
            position: absolute;
            top: 50%;
            left: 0;
            width: 100%;
            height: 1px;
            border: 1px dashed #E5E5E5;
          }
          p {
            position: relative;
            z-index: 1;
            font-size: 14px;
            line-height: 120%;
            font-weight: 300;
            background: #fff;
          }
          p:nth-child(1) {
            padding-right: 4px;
          }
          p:nth-child(2) {
            padding-left: 4px;
            span {
              font-weight: 400;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .product-main {
    padding-bottom: 20px;
    &__top {
      margin-top: 20px;
    }
    &__btns {
      grid-template-columns: 100%;
      gap: 10px;
    }
  }
  .products2 {
    padding: 20px 0;
    &__cards {
      display: grid;
      grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
      gap: 20px;
      margin-top: 20px;
    }
    &__btn {
      margin-top: 20px;
    }
  }
  .expert {
    padding: 20px 0 40px;
    &__inner {
      padding: 35px 45px;
    }
    &__text {
      margin-top: 20px;
      p {
        font-size: 14px;
        line-height: 150%;
      }
    }
    &__slider {
      padding: 0;
    }
    &__user {
      &-name {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .product-main {
    &__group {
      width: auto;
    }
    &__wrap {
      flex-direction: column;
    }
    &__slider {
      width: 100%;
    }
    &__desc {
      width: 100%;
    }
  }
  .products2 {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
  .expert {
    &__inner {
      overflow: hidden;
    }
    .swiper {
      overflow: unset;
    }
  }
  .galley-modal {
    &__title {
      padding: 24px 48px 24px 16px;
    }
    .modal__close {
      right: 16px;
    }
    .modal__content {
      padding: 24px 32px;
    }
    .product-main__slider {
      height: 500px;
    }
    .swiper-button-prev {
      left: 14px;
    }
    .swiper-button-next {
      right: 14px;
    }
  }
}

@media screen and (max-width: 576px) {
  .product-main {
    &__top {
      flex-wrap: wrap;
      gap: 10px 16px;
    }
  }
  .products2 {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
      gap: 15px;
    }
  }
  .expert {
    &__inner {
      padding: 30px;
    }
  }
  .galley-modal {
    .product-main__slider {
      height: auto;
      flex-direction: column;
    }
    .modal__content {
      padding: 24px 16px;
    }
    .mySwiper {
      width: 100%;
      order: 1;
    }
    .mySwiper2 {
      width: 100%;
    }
    .swiper-button-prev {
      left: 10px;
    }
    .swiper-button-next {
      right: 10px;
    }
  }
}

@media screen and (max-width: 399px) {
  .product-main {
    &__group {
      width: 100%;
      gap: 8px;
    }
  }
}

@media screen and (max-width: 359px) {
  .product-main {
    .calc-info {
      &__hidden {
        right: 0;
        &:after {
          right: 7px;
        }
      }
    }
  }
  .products2 {
    &__cards {
      grid-template-columns: 100%;
    }
    &__btn {
      padding-left: 18px;
      padding-right: 18px;
    }
  }
}