.case-main {
  padding-bottom: 30px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 16px;
  }
  &__send {
    width: 22px;
    flex-shrink: 0;
    background: transparent;
    padding: 0;
    margin-top: 24px;
    img {
      width: 100%;
    }
  }
  &__img {
    margin-top: 40px;
    img {
      width: 100%;
    }
  }
  &__text {
    max-width: 990px;
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    text-align: center;
    margin: 70px auto 0;
    p {
      font-size: 22px;
      line-height: 140%;
      font-weight: 300;
    }
  }
  &__tags {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 10px 20px;
    margin-top: 25px;
  }
  &__tag {
    display: flex;
    align-items: center;
    gap: 7px;
    font-size: 14px;
    line-height: 120%;
    font-weight: 300;
    span {
      font-weight: 400;
      text-transform: uppercase;
      background: #F1F1F1;
      padding: 4px 10px;
    }
  }
  .article-gallery__slider {
    margin-top: 60px;
    .swiper-button-next,
    .swiper-button-prev {
      display: none;
    }
  }
  @media screen and (min-width: 577px) {
    .mySwiper2 {
      .swiper-wrapper {
        transform: unset !important;
        display: grid;
        grid-template-columns: repeat(9, calc(100% / 9 - 240px / 9));
        gap: 30px;
      }
    }
    .mySwiper {
      display: none;
    }
    .swiper-slide {
      width: 100% !important;
      padding-bottom: 58%;
      &:nth-child(1) {
        grid-column: 1/7;
      }
      &:nth-child(2) {
        grid-column: 7/10;
      }
      &:nth-child(3) {
        grid-column: 1/6;
      }
      &:nth-child(4) {
        grid-column: 6/10;
      }
      &:nth-child(5) {
        grid-column: 1/4;
      }
      &:nth-child(6) {
        grid-column: 4/10;
      }
      &:nth-child(6) ~ .swiper-slide {
        display: none;
      }
    }
    .swiper-pagination {
      display: none;
    }
  }
}
.case-info {
  padding: 30px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 30px;
  }
  &__left {
    width: calc(100% - 408px);
  }
  &__right {
    position: relative;
    width: 378px;
  }
  &__group {
    &:not(:first-child) {
      margin-top: 50px;
    }
  }
  &__text {
    margin-top: 12px;
    p {
      font-size: 18px;
      line-height: 140%;
      font-weight: 300;
    }
  }
  &__gallery {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 30px / 2));
    gap: 30px;
    margin-top: 30px;
  }
  &__img {
    position: relative;
    padding-bottom: 96%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__img--big {
    grid-column: 1/3;
    padding-bottom: 46%;
  }
  &__group2 {
    position: sticky;
    top: 10px;
    border: 1px solid #E5E5E5;
    padding: 32px 24px;
  }
  &__subtitle {
    font-family: 'PlayfairDisplay';
    font-size: 30px;
    line-height: 120%;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin: 24px 0 0 0;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 20px;
    &-icon {
      width: 42px;
      height: 42px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #EFEFEF;
      border-radius: 50%;
      img {
        max-width: 24px;
      }
    }
    &-text {
      font-size: 14px;
      line-height: 140%;
      font-weight: 300;
    }
  }
  &__btns {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 30px;
  }
  &__btn {
    width: 100%;
    min-height: 56px;
    gap: 10px;
    padding: 14px;
    &:hover {
      i svg path {
        fill: #fff;
      }
    }
    i {
      width: 25px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 992px) {
  .case-main {
    &__title {
      font-size: 50px;
    }
    &__text {
      margin-top: 60px;
      p {
        font-size: 18px;
      }
    }
    .article-gallery__slider {
      margin-top: 45px;
    }
    @media screen and (min-width: 577px) {
      .mySwiper2 {
        .swiper-wrapper {
          grid-template-columns: repeat(9, calc(100% / 9 - 192px / 9));
          gap: 24px;
        }
      }
    }
  }
  .case-info {
    &__left {
      width: 100%;
    }
    &__right {
      display: none;
    }
  }
}

@media screen and (max-width: 576px) {
  .case-main {
    padding-bottom: 20px;
    &__top {
      align-items: center;
    }
    &__title {
      font-size: 30px;
    }
    &__send {
      margin-top: 0;
    }
    &__img {
      margin-top: 30px;
    }
    &__text {
      margin-top: 30px;
      p {
        font-size: 16px;
      }
    }
    &__tag {
      font-size: 12px;
      span {
        padding: 5px 10px;
      }
    }
  }
  .case-info {
    padding: 20px 0;
    &__group {
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
    &__gallery {
      grid-template-columns: 100%;
      gap: 10px;
      margin-top: 20px;
    }
    &__img--big {
      display: none;
      grid-column: unset;
    }
  }
}