.info {
  padding-bottom: 80px;
  &__title {
    font-weight: 500;
  }
  .map-wrap {
    position: relative;
    padding-bottom: 57.1%;
    margin-top: 25px;
    .map {
      position: absolute;
      z-index: 100;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
    }
    @media screen and (min-width: 577px) {
      .ymaps-2-1-79-balloon {
        top: -40px !important;
        left: 70px !important;
      }
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 70px;
  }
  .name-title {
    font-weight: 400;
    margin-bottom: 14px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 14px;
  }
  &__item {
    font-family: 'Circe';
    font-size: 16px;
    line-height: 125%;
    span {
      font-weight: 300;
    }
    .info-phone {
      color: #E22429;
      text-decoration: none !important;
    }
    br {
      display: none;
    }
  }
}
.info-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
  &__desc {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
  }
  &__img {
    position: relative;
    max-width: 380px;
    width: 100%;
    height: 190px;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__btns {
    max-width: 280px;
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
  }
  &__btn {
    padding: 19px 34px;
    span {
      font-size: 18px;
    }
  }
}
.info-main {
  padding-bottom: 40px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 30px;
    margin-bottom: 30px;
  }
  &__title {
    font-weight: 500;
    margin-bottom: 20px;
  }
  &__group2 {
    flex-shrink: 0;
    padding-top: 15px;
  }
  &__btn {
    padding: 19px 45px;
    span {
      font-size: 18px;
    }
  }
  &__text {
    margin-top: 10px;
    p {
      font-size: 16px;
      line-height: 120%;
    }
  }
  &__img {
    position: relative;
    padding-bottom: 57%;
    margin: 30px 0 50px;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 30px / 2));
    gap: 30px;
    margin-top: 30px;
    margin-bottom: 50px;
  }
  &__item {
    position: relative;
    padding-bottom: 57%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  .map-wrap {
    position: relative;
    padding-bottom: 57.1%;
    margin-top: 25px;
    .map2 {
      position: absolute;
      z-index: 100;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
    }
    @media screen and (min-width: 577px) {
      .ymaps-2-1-79-balloon {
        top: -40px !important;
        left: 70px !important;
      }
    }
  }
  .balloon2-mob {
    display: none;
    align-items: center;
    gap: 20px;
    margin-top: 20px;
    .balloon2__text {
      margin-top: 0;
    }
  }
}
.ymaps-2-1-79-balloon__content {
  max-width: 320px !important;
}
.ymaps-2-1-79-balloon__tail {
  &:after {
    display: none;
  }
}
.balloon {
  width: 100%;
  &__wrap {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  .name-title {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 12px;
    span {
      width: 20px;
      img {
        width: 100%;
      }
    }
  }
  .info__items {
    gap: 10px;
  }
  .info__item {
    font-size: 12px;
    line-height: 116%;
    strong {
      font-weight: 400;
    }
    br {
      display: block;
    }
  }
  &__btn {
    width: 100%;
    display: none;
    margin-top: 15px;
    span {
      font-size: 18px;
    }
  }
}
.balloon2 {
  font-family: 'Circe';
  &__btn {
    padding-left: 34px;
    padding-right: 34px;
    span {
      font-size: 18px;
    }
  }
  &__text {
    font-size: 16px;
    line-height: 125%;
    font-weight: 300;
    margin-top: 10px;
  }
}

@media screen and (max-width: 992px) {
  .info {
    padding-bottom: 40px;
    &__title {
      font-size: 60px;
    }
    &__cards {
      margin-top: 40px;
    }
    &__item {
      br {
        display: block;
      }
    }
  }
  .info-card {
    gap: 25px;
    &__img {
      max-width: 220px;
      height: 320px;
    }
    &__desc {
      flex-direction: column;
      align-items: start;
      gap: 16px;
    }
    &__btns {
      max-width: 100%;
    }
  }
  .info-main {
    &__top {
      gap: 20px;
    }
    &__title {
      font-size: 60px;
    }
    &__btn {
      padding: 19px 30px;
    }
    &__items {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
      gap: 15px;
    }
    .ymaps-2-1-79-balloon {
      display: none;
    }
    .balloon2-mob {
      display: flex;
    }
  }
}

@media screen and (max-width: 768px) {
  .info {
    .map-wrap {
      width: calc(100% + 30px);
      background: #F5F5F5;
      padding-bottom: 65%;
      margin-left: -15px;
      .map {
        padding: 20px 15px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .info {
    .map-wrap {
      min-height: 464px;
      padding-bottom: 80%;
    }
    &__title {
      font-size: 32px;
    }
    &__cards {
      gap: 30px;
    }
  }
  .info-card {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    &__img {
      max-width: 100%;
      height: 170px;
    }
    &__btns {
      width: 100%;
    }
  }
  .info-main {
    &__top {
      flex-direction: column;
    }
    &__group2 {
      width: 100%;
    }
    &__btn {
      width: 100%;
    }
    &__title {
      font-size: 32px;
    }
    &__subtitle {
      font-size: 24px;
    }
    &__items {
      grid-template-columns: 100%;
    }
    .balloon2-mob {
      flex-direction: column;
      align-items: start;
      .balloon2__btn {
        width: 100%;
      }
    }
  }
  .balloon {
    &__btn {
      display: flex;
    }
  }
}

@media screen and (max-width: 450px) {
  .info {
    .map-wrap {
      padding-bottom: 100%;
    }
  }
  .ymaps-2-1-79-balloon__content {
    ymaps {
      height: auto !important;
    }
  }
}