.h1 {
  font-family: 'PlayfairDisplay';
  font-size: 60px;
  line-height: 125%;
  letter-spacing: 2.4px !important;
}
h2 {
  font-size: 43px;
  line-height: 120%;
  font-variant: lining-nums;
}
h3 {
  font-size: 38px;
  line-height: 120%;
  font-variant: lining-nums;
}
h4 {
  font-size: 33px;
  line-height: 120%;
  font-variant: lining-nums;
}
h5 {
  font-size: 28px;
  line-height: 120%;
  font-variant: lining-nums;
}
h6 {
  font-size: 23px;
  line-height: 120%;
  font-weight: 700;
  font-variant: lining-nums;
}
.btn-black {
  background: #2B2A29F2;
  border-radius: 0;
  padding: 19.5px;
  span {
    line-height: 105%;
    color: #fff;
    text-transform: uppercase !important;
  }
  &:hover {
    background: #000;
  }
}
.btn-black__outline {
  background: transparent;
  border: 1px solid #2B2A29F2;
  border-radius: 0;
  padding: 18px;
  span {
    line-height: 105%;
    color: #2B2A29F2;
    text-transform: uppercase;
  }
  &:hover {
    background: #2B2A29F2;
    span {
      color: #fff;
    }
  }
}
.text43 {
  font-family: 'PlayfairDisplay';
  font-size: 43px;
  line-height: 120%;
}
.text30 {
  font-family: 'PlayfairDisplay';
  font-size: 30px;
  line-height: 120%;
}
.text20 {
  font-size: 20px;
  line-height: 140%;
}
.modal {
  background: rgba(#000, .3);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 582px;
    max-width: 100%;
    position: relative;
    background: #ffffff;
    margin: auto;
    padding: 32px 50px 50px;
    @media screen and (max-width: 992px) {
      max-width: calc(100% - 30px);
      margin: 50px auto;
    }
  }
  &__close {
    position: absolute;
    top: 20px;
    right: 16px;
    width: 16px;
    background: transparent;
    padding: 0;
    img {
      width: 100%;
    }
  }
  &__title {
    position: relative;
    font-family: 'PlayfairDisplay';
    font-size: 35px;
    line-height: 120%;
    padding-bottom: 24px;
    margin-bottom: 34px;
    &:after {
      content: '';
      position: absolute;
      left: -50px;
      bottom: 0;
      width: calc(100% + 100px);
      border-bottom: 1px solid #E5E5E5;
    }
  }
  &__text {
    font-size: 16px;
    line-height: 120%;
  }
}
.form {
  display: grid;
  grid-template-columns: 100%;
  gap: 24px;
  &-group {
    position: relative;
    margin: 0;
    label {
      position: relative;
      font-size: 16px;
      line-height: 120%;
      font-weight: 400;
      color: #83878D;
      span {
        content: '';
        position: absolute;
        top: 7px;
        right: -12px;
        width: 4px;
        height: 4px;
        background: #E22429;
        border-radius: 50%;
      }
    }
    input {
      width: 100%;
      height: 40px;
      border: none;
      border-bottom: 1px solid #83878D;
      font-size: 16px;
      line-height: 120%;
      color: #2B2A29;
      &::placeholder {
        color: rgba(131, 135, 141, 0.5);
      }
    }
    textarea {
      width: 100%;
      height: 75px;
      border: none;
      border-bottom: 1px solid #83878D;
      padding: 8px 0;
      font-size: 16px;
      line-height: 120%;
      color: #2B2A29;
      resize: none;
      &::placeholder {
        color: rgba(131, 135, 141, 0.5);
      }
    }
    .password-icon {
      position: absolute;
      z-index: 3;
      right: 8px;
      bottom: 8px;
      width: 24px;
      cursor: pointer;
      &--active {
        &:after {
          display: none;
        }
      }
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(45deg);
        width: 1.5px;
        height: 24px;
        background: #2B2A29;
      }
      img {
        width: 100%;
      }
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        height: 100%;
        padding: 0;
        margin: 0;
        opacity: 0;
        cursor: pointer;
      }
    }
  }
  .password-group {
    input {
      padding-right: 40px;
    }
  }
  &__check {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
      cursor: pointer;
    }
    input:checked ~ label {
      &:after {
        background-image: url("../img/check.svg");
      }
    }
    label {
      position: relative;
      display: block;
      font-size: 12px;
      line-height: 120%;
      font-weight: 400;
      padding-left: 35px;
      &:after {
        content: '';
        position: absolute;
        top: -4px;
        left: 0;
        width: 18px;
        height: 18px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        border: 1px solid #2B2A29;
        border-radius: 4px;
      }
      a {
        position: relative;
        z-index: 3;
        color: #2b2a29 !important;
        border-bottom: 1px solid transparent;
        text-decoration: unset !important;
        &:hover {
          border-color: #e22429;
        }
      }
    }
  }
  &__btn {
    span {
      font-size: 18px;
    }
  }
  &__file {
    &-wrap {
      position: relative;
      display: inline-flex;
      flex-direction: column;
      &:hover {
        label {
          &:after {
            display: none;
          }
        }
      }
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        opacity: 0;
      }
      label {
        position: relative;
        display: flex;
        gap: 8px;
        font-size: 14px;
        line-height: 120%;
        color: #2B2A29;
        &:after {
          content: '';
          position: absolute;
          left: 0;
          bottom: -1px;
          width: 100%;
          height: 1px;
          background: #DB161B;
        }
        i {
          width: 15px;
          flex-shrink: 0;
          img {
            width: 100%;
          }
        }
        span {
          position: static;
          width: auto;
          height: auto;
          background: transparent;
          color: #2B2A29;
        }
      }
    }
  }
}
.language-modal {
  .modal__dialog {
    width: 390px;
    padding: 30px;
  }
  .modal__title {
    font-size: 23px;
    padding-bottom: 0;
    margin-bottom: 20px;
    &:after {
      display: none;
    }
  }
  &__btns {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 16px / 2));
    gap: 16px;
  }
  &__btn {
    padding: 14px;
    span {
      font-size: 14px;
    }
  }
}
.authorization-modal {
  .modal__dialog {
    width: 550px;
  }
  .modal__text {
    max-width: 280px;
    margin: 0 auto;
  }
  .modal-form {
    max-width: 280px;
    margin: 20px auto 0;
  }
  &__article {
    border: 1px solid #E5E5E5;
    padding: 20px;
    &-title {
      font-family: 'PlayfairDisplay';
      font-size: 16px;
      line-height: 120%;
      color: #83878D;
    }
    &-text {
      margin-top: 10px;
      p {
        font-size: 17px;
        line-height: 120%;
      }
    }
  }
}
.fancybox-content {
  position: unset !important;
  background: transparent !important;
  padding: 0 !important;
}
.fancybox-slide {
  display: flex !important;
}
.fancybox-button {
  svg path {
    fill: #fff !important;
  }
}
.swiper-button-next,
.swiper-button-prev {
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.5);
  border: 1px solid rgba(246, 246, 246, 1);
  border-radius: 50%;
  &:after {
    display: none;
  }
  &:hover {
    background: rgba(246, 246, 246, 1);
  }
  img {
    width: 10px;
  }
}
.favorite--active {
  svg path {
    fill: #2B2A29F2;
    stroke: #2B2A29F2;
    fill-opacity: 1;
  }
}
.input-group {
  border-bottom: 1px solid #83878D;
  input {
    width: 100%;
    height: 40px;
    background: transparent !important;
    border: none !important;
    box-shadow: unset !important;
    padding: 0;
    font-size: 16px;
    line-height: 120%;
    color: #2B2A29;
    &::placeholder {
      opacity: 0;
      color: #ff0033;
    }
    &::selection {
      background: transparent !important;
    }
  }
}
#btn-country {
  align-items: center;
  border: none;
  padding: 0;
  .flag {
    order: -2;
  }
  .caret {
    order: -1;
  }
}
.dropdown-menu {
  top: calc(100% - 5px);
  display: block;
  opacity: 0;
  pointer-events: none;
  margin-top: 0;
  &--active {
    opacity: 1;
    pointer-events: auto;
  }
}
.nice-select {
  width: 100%;
  height: auto;
  background: transparent;
  border: none;
  border-bottom: 1px solid #83878D;
  border-radius: 0;
  padding: 0 0 10px 0;
  &:after {
    right: 6px;
    transform: rotate(0);
    background-image: url("../img/arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto;
  }
  .current {
    font-size: 18px;
    line-height: 120%;
    font-weight: 400;
  }
}
.pagination {
  display: flex;
  align-items: center;
  gap: 18px;
  &__items {
    display: flex;
    align-items: center;
    gap: 4px;
  }
  &__prev,
  &__next {
    width: 20px;
    flex-shrink: 0;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__item {
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-size: 18px;
    line-height: 105%;
    font-weight: 300;
    text-decoration: unset !important;
    &--active {
      background: #F6F6F6;
      font-weight: 700;
    }
    &:hover {
      background: #F6F6F6;
      font-weight: 700;
    }
  }
}
.connection-fixed {
  position: fixed;
  z-index: 10;
  top: -105%;
  left: 0;
  width: 100%;
  background: #fff;
  border-bottom: 1px solid #BCBCBC;
  padding: 14px 0;
  pointer-events: none;
  transition: all 0.5s;
  &--active {
    top: 0;
    pointer-events: auto;
  }
  &--active2 {
    .remove-btn {
      display: none;
    }
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
  h6 {
    font-weight: 400;
  }
  .gifts-main__btns {
    display: flex;
    margin-top: 0;
  }
  .gifts-main__btn {
    min-height: 38px;
    gap: 10px;
    padding: 5px 15px;
    span {
      font-size: 14px;
    }
  }
}
#files-area {
  margin-bottom: 12px;
}
#files-names {
  .file-block {
    position: relative;
    display: table;
    font-size: 14px;
    line-height: 120%;
    color: #83878D;
    padding-left: 21px;
    padding-right: 16px;
    &:not(:first-child) {
      margin-top: 5px;
    }
    &:after {
      content: '';
      position: absolute;
      top: 2px;
      left: 0;
      width: 13px;
      height: 13px;
      background-image: url("../img/file-icon2.svg");
      background-repeat: no-repeat;
      background-position: left;
      background-size: contain;
    }
  }
  .file-delete {
    position: absolute;
    z-index: 5;
    top: 2px;
    right: 0;
    transform: rotate(-45deg);
    font-size: 18px;
    line-height: 100%;
    cursor: pointer;
  }
}


@media screen and (max-width: 992px) {
  .h1 {
    font-size: 40px;
  }
  .text30 {
    font-size: 24px;
  }
  .modal {
    &__dialog {
      width: 400px;
      padding: 32px;
    }
    &__title {
      font-size: 30px;
      &:after {
        left: -32px;
        width: calc(100% + 64px);
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .h1 {
    font-size: 32px;
    letter-spacing: unset !important;
  }
  h2 {
    font-size: 30px;
  }
  h3 {
    font-size: 28px;
  }
  h4 {
    font-size: 26px;
  }
  h5 {
    font-size: 24px;
  }
  h6 {
    font-size: 20px;
  }
  .text43 {
    font-size: 30px;
  }
  .text20 {
    font-size: 18px;
  }
  .modal {
    &__dialog {
      width: 100%;
    }
    &__title {
      font-size: 24px;
    }
  }
  .connection-fixed {
    padding: 10px 0;
    &--active2 {
      .gifts-main__btns {
        grid-template-columns: 100% !important;
      }
    }
    &__title {
      display: none;
    }
    .gifts-main__btns {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 8px / 2));
      gap: 8px;
    }
    .gifts-main__btn {
      padding: 5px;
      span {
        font-size: 12px;
      }
    }
  }
}

@media screen and (max-width: 399px) {
  .language-modal {
    &__btns {
      grid-template-columns: 100%;
      gap: 8px;
    }
  }
}

@media screen and (max-width: 359px) {
  .pagination {
    gap: 12px;
  }
}