.gifts-main {
  padding-bottom: 20px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
  }
  &__desc {
    width: 48%;
  }
  &__img {
    width: 52%;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  &__title {
    font-size: 50px;
    line-height: 120%;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 20px;
    p {
      font-size: 23px;
      line-height: 140%;
      font-weight: 300;
    }
  }
  &__btns {
    max-width: 482px;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 10px / 2));
    gap: 10px;
    margin-top: 40px;
  }
  &__btn {
    gap: 10px;
    padding: 15px 5px;
    &:hover {
      i svg path {
        fill: #fff;
      }
    }
    span {
      font-size: 18px;
      text-transform: uppercase;
    }
  }
}
.projects {
  padding: 15px 0 30px;
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
    gap: 35px 30px;
    margin-top: 35px;
  }
  &__bottom {
    display: flex;
    justify-content: center;
    margin-top: 35px;
  }
  &__btn {
    padding: 18px 60px;
    span {
      font-size: 18px;
    }
  }
}
.project-card {
  &__img {
    position: relative;
    padding-bottom: 106%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__desc {
    margin-top: 15px;
  }
  &__title {
    font-size: 24px;
    line-height: 145%;
    text-transform: uppercase;
  }
  &__text {
    margin-top: 5px;
    p {
      font-size: 16px;
      line-height: 150%;
      color: #7A7773;
    }
  }
}
.gifts-banner {
  padding: 30px 0;
  .banner__inner {
    background-image: url("../img/gifts-banner__bg.webp");
    background-repeat: no-repeat;
    background-position: right center;
    background-size: cover;
  }
  .banner__desc {
    max-width: 585px;
  }
  .banner__text {
    p {
      font-size: 20px;
      font-weight: 300;
      color: #fff;
    }
  }
  .banner__btn {
    display: inline-flex;
    margin-top: 40px;
  }
}
.gifts-advantages {
  padding: 30px 0 40px;
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
    gap: 30px;
    margin-top: 30px;
  }
  .certificate-info__card {
    align-items: center;
  }
  .certificate-info__card-text {
    margin-top: 0;
  }
}
.gifts-products {
  padding: 40px 0 30px;
  overflow: hidden;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 16px;
  }
  &__text {
    max-width: 800px;
    margin-top: 24px;
    p {
      font-size: 18px;
      line-height: 130%;
      font-weight: 300;
    }
  }
  &__slider-btns {
    flex-shrink: 1;
    display: flex;
    align-items: center;
    gap: 8px;
  }
  .swiper-button-prev,
  .swiper-button-next {
    position: static;
  }
  .products__slider {
    margin-top: 30px;
  }
  &__bottom {
    display: flex;
    justify-content: center;
    margin-top: 40px;
  }
  &__btn {
    min-width: 347px;
    padding-left: 26px;
    padding-right: 26px;
    span {
      font-size: 18px;
    }
  }
}
.scheme {
  padding: 30px 0 40px;
  &__items {
    display: grid;
    grid-template-columns: repeat(5, calc(20% - 80px / 5));
    gap: 20px;
    margin-top: 50px;
  }
  &__item {
    &:last-child {
      .scheme__item-number {
        &:after {
          display: none;
        }
      }
    }
    &-number {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 20px;
        left: 50px;
        width: calc(100% - 40px);
        height: 1px;
        background: #000000;
      }
      span {
        width: 40px;
        height: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #E5E5E5;
        border-radius: 50%;
        font-family: 'PlayfairDisplay';
        font-size: 26px;
        line-height: 105%;
        font-variant: lining-nums;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 130%;
      margin-top: 15px;
    }
  }
}
.gifts-connection {
  padding: 40px 0 30px;
  overflow: hidden;
  &__inner {
    border: 1px solid #000000;
    padding: 40px 70px;
  }
  &__title {
    text-align: center;
  }
  &__form {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 30px / 2));
    gap: 24px 30px;
    margin-top: 30px;
    .form__file {
      margin-bottom: 24px;
    }
    .form__btn {
      width: 100%;
      padding: 18px;
    }
  }
}
.gifts-questions {
  padding: 30px 0 80px;
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 30px / 2));
    gap: 30px;
    align-items: start;
    margin-top: 60px;
  }
  &__items-left,
  &__items-right {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
  }
  &__item {
    border-bottom: 1px solid #000000;
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 8px;
      padding-bottom: 24px;
      cursor: pointer;
    }
    &-title {
      font-size: 18px;
      line-height: 120%;
    }
    &-btn {
      width: 12px;
      flex-shrink: 0;
      transition: all 0.3s;
      img {
        width: 100%;
      }
    }
    &-hidden {
      display: none;
      padding-bottom: 24px;
    }
    &-text {
      p {
        font-size: 16px;
        line-height: 140%;
        font-weight: 300;
      }
    }
  }
  .item--active {
    .gifts-questions__item-btn {
      transform: rotate(-180deg);
    }
  }
}


@media screen and (max-width: 1200px) {
  .gifts-advantages {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
      gap: 24px;
    }
    .certificate-info__card {
      padding: 20px;
    }
  }
}

@media screen and (max-width: 992px) {
  .gifts-main {
    &__inner {
      flex-direction: column;
      align-items: start;
    }
    &__desc {
      width: 100%;
    }
    &__img {
      width: 100%;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
    &__text {
      p {
        font-size: 20px;
      }
    }
    &__btns {
      margin-top: 20px;
    }
  }
  .projects {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
      gap: 35px 24px;
      margin-top: 35px;
    }
    &__btn {
      padding-left: 35px;
      padding-right: 35px;
    }
  }
  .project-card {
    &:nth-child(8) ~ .project-card {
      display: none;
    }
  }
  .gifts-banner {
    .banner__inner {
      background-image: url("../img/gifts-banner__bg2.webp");
      background-position: center bottom;
      padding: 60px 60px 250px;
    }
    .banner__desc {
      max-width: 100%;
    }
  }
  .gifts-advantages {
    padding-bottom: 30px;
    @media screen and (min-width: 577px) {
      .certificate-info__card {
        flex-direction: column;
        align-items: start;
        gap: 30px;
      }
    }
    .certificate-info__card-text {
      p {
        font-size: 16px;
      }
    }
  }
  .gifts-products {
    padding-top: 30px;
    .swiper {
      overflow: unset;
    }
  }
  .scheme {
    &__item {
      &-text {
        font-size: 14px;
      }
    }
  }
  .gifts-connection {
    &__inner {
      padding: 40px 32px;
    }
    &__form {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
      gap: 24px;
    }
  }
  .gifts-questions {
    &__items {
      grid-template-columns: 100%;
      gap: 24px;
    }
  }
}

@media screen and (max-width: 700px) {
  .gifts-banner {
    .banner__inner {
      text-align: start;
    }
    .banner__desc {
      display: block;
      padding: 0;
    }
  }
  .gifts-advantages {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 32px / 3));
      gap: 16px;
    }
  }
  .scheme {
    &__items {
      grid-template-columns: 100%;
      gap: 16px;
      margin-top: 32px;
    }
    &__item {
      display: flex;
      align-items: center;
      gap: 16px;
      &-number {
        &:after {
          top: 42px;
          left: 20px;
          width: 1px;
          height: 12px;
        }
      }
      &-text {
        margin-top: 0;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .gifts-main {
    &__inner {
      gap: 40px;
    }
    &__title {
      font-size: 30px;
    }
    &__text {
      p {
        font-size: 16px;
      }
    }
    &__btns {
      max-width: 100%;
      grid-template-columns: 100%;
      gap: 16px;
    }
  }
  .projects {
    padding: 20px 0;
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 16px;
      margin-top: 16px;
    }
    &__bottom {
      margin-top: 16px;
    }
  }
  .project-card {
    &__title {
      font-size: 14px;
    }
    &__text {
      p {
        font-size: 14px;
      }
    }
  }
  .gifts-banner {
    padding: 20px 0;
    .banner__inner {
      padding: 48px 38px 208px;
    }
    .banner__text {
      p {
        font-size: 16px;
      }
    }
    .banner__btn {
      width: 100%;
      margin-top: 20px;
    }
  }
  .gifts-advantages {
    padding: 20px 0;
    &__cards {
      grid-template-columns: 100%;
      margin-top: 16px;
    }
    .certificate-info__card {
      gap: 30px;
    }
  }
  .gifts-products {
    padding: 20px 0;
    &__text {
      margin-top: 10px;
      p {
        font-size: 16px;
      }
    }
    .products__slider {
      margin-top: 20px;
    }
  }
  .scheme {
    padding: 20px 0;
  }
  .gifts-connection {
    padding: 20px 0;
    &__inner {
      padding: 30px 20px;
    }
    &__form {
      grid-template-columns: 100%;
      margin-top: 24px;
    }
  }
  .gifts-questions {
    padding: 20px 0 40px;
    &__items {
      margin-top: 40px;
    }
    &__item {
      &-title {
        font-size: 16px;
      }
      &-hidden {
        padding-bottom: 16px;
      }
      &-text {
        p {
          font-size: 14px;
        }
      }
    }
  }
}

@media screen and (max-width: 399px) {
  .projects {
    &__cards {
      grid-template-columns: 100%;
    }
    &__btn {
      width: 100%;
    }
  }
  .project-card {
    &:nth-child(5) ~ .project-card {
      display: none;
    }
  }
  .gifts-banner {
    .banner__inner {
      background-image: url("../img/gifts-banner__bg3.webp");
    }
  }
  .gifts-products {
    &__btn {
      width: 100%;
      min-width: unset;
    }
  }
}